.loading {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: var(--bg-page);
  z-index: 999;
}

.loaderText {
  position: fixed;
  top: 80%;
  left: 50%;
  // margin-left: -4em;
  transform: translate(-50%, -50%);
  // background-color: #fff;
  // font-size: 10px;
  // border: 0.8em solid rgb(61, 65, 80);
  // border-left: 0.8em solid rgba(58, 166, 165, 1);
}

.loader {
  left: 50%;
  margin-left: -4em;
  font-size: 10px;
  border: 0.8em solid rgb(61, 65, 80);
  border-left: 0.8em solid rgba(58, 166, 165, 1);
  animation: spin 1.1s infinite linear;
}
.loader,
.loader:after {
  border-radius: 50%;
  width: 8em;
  height: 8em;
  display: block;
  position: absolute;
  top: 50%;
  margin-top: -4.05em;
}

@keyframes spin {
  0% {
    transform: rotate(360deg);
  }
  100% {
    transform: rotate(0deg);
  }
}
